
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

export default {
name: "subStoreList",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading
},
data() {
return {
isShadow: false,
isPwd: 0,
tel:13726768507,
num: 60,
code:''
};
},
created() {

},
mounted() {
this.$nextTick(function() {});
},
methods: {
countDown () {
console.log(888)
console.log(this.num)
let c = this.num
c--
if ( c < 1) {



this.num=60;
return
}




this.num=c;

console.log('c',c)

setTimeout(()=>{
this.countDown();
},1000)
},
async getCode () {
this.countDown();
var tel_user = this.Trim(this.tel);
var that=this;

var param={
tel:tel_user,
plat:3,
type:1,//类型1设置支付密码验证码，2找回支付密码验证码，3提现验证码
time:new Date().getTime(),
version:'1.0.1',
device_name:'MAX123'
};

param=JSON.stringify(param);
param=this.$encrypt(param);

param={
param:param
};
const [err, res] = await api.sendTradePassword(param);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
console.log('发送验证码中')
}


},
shadowToggle() {
this.isShadow = true;
},
async checkSMS(){
console.log(this.tel,this.code)
if(this.tel=='' || !this.tel){
this.$vux.toast.text('电话号码不能为空');
return false;
}else if(this.code=='' || !this.code){
this.$vux.toast.text('验证码不能为空');
return false;
}else{
var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;
var tel_user = this.Trim(this.tel);
if (!phoneReg.test(tel_user)) {
this.$vux.toast.text('请输入有效的手机号！');
return false;
}else{
var data={
tel:this.tel,
plat: 3,
type: 1,//1设置支付密码验证码，2找回支付密码验证码
code: this.code
};
const [err, res] = await api.checkTradePassword(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
this.$router.push("./setTradeCode");
}
}

}
}
},

Trim(str) {
str=String(str);
return str.replace(/(^\s*)|(\s*$)/g, "");
},

cancelshadowToggle() {
this.isShadow = false;
}
},
filters: {},
computed: {
...mapGetters(["user", "account", "token"])
}
};



